@import url(https://fonts.googleapis.com/css2?family=Encode+Sans:wght@300;400;500;600;700;900&display=swap);
*,::before,::after{margin:0;padding:0;box-sizing:border-box}body{font-family:"Encode Sans",sans-serif}ul{margin:0;padding:0;list-style:none}#header{overflow:hidden}#header .navbar{background:#000406}#header .active__menu{color:#fff;opacity:.9;border-bottom:2px solid #01b566}#header .animated__logo{font-size:50px}#header .animated__logo{color:#01b666;animation:myMove 10s linear infinite both;-webkit-animation:myMove 10s linear infinite both}#header .bars{color:#b7b7b7;font-size:35px}#header .navbar-dark .navbar-toggler{box-shadow:none;border-color:transparent}#header ul li a{text-transform:capitalize;font-size:18px;color:#b7b7b7}#header .animated__logo:hover{color:#01b566;opacity:.9}#header form .btn.btn-outline-success{font-weight:600;padding:7px 30px;background:#01b566;border-radius:30px;-webkit-border-radius:30px;-moz-border-radius:30px;-ms-border-radius:30px;-o-border-radius:30px;transition:.3s;-webkit-transition:.3s;-moz-transition:.3s;-ms-transition:.3s;-o-transition:.3s}#header form .btn.btn-outline-success:hover{background:#01b566;opacity:.9}@-webkit-keyframes myMove{100%{transform:rotate(360deg);-webkit-transform:rotate(360deg);-moz-transform:rotate(360deg);-ms-transform:rotate(360deg);-o-transform:rotate(360deg)}}@keyframes myMove{100%{transform:rotate(360deg);-webkit-transform:rotate(360deg);-moz-transform:rotate(360deg);-ms-transform:rotate(360deg);-o-transform:rotate(360deg)}}#banner{overflow:hidden;position:relative;width:100%;min-height:100vh;display:flex;align-items:center;justify-content:center}#banner .video__wrap video{position:absolute;top:0;left:0;width:100%;height:100%;object-fit:cover;pointer-events:none;z-index:-1}#banner .dark__overlay{z-index:-1;background:#000;opacity:.6;position:absolute;top:0;left:0;height:100%;width:100vw}#banner .main__content h2{color:#fff;font-size:68px;font-weight:700}#banner .main__content p{color:#a8b1ae;padding:0 19%;text-align:center;font-size:20px}#banner .main__content a.btn.btn-light{font-weight:bold;border:1px solid #01b767;padding:15px 35px;background:#01b767;border-radius:50px;-webkit-border-radius:50px;-moz-border-radius:50px;-ms-border-radius:50px;-o-border-radius:50px}#banner .main__content .angle{margin-left:7px;font-size:20px}#banner .main__content a.btn.btn-light:hover{opacity:.8;background:#fff}#about,#signUp{overflow:hidden;padding-top:120px;padding-bottom:80px;background:#000406}#about .about__left h6,#signUp .about__left h6{font-weight:bold;text-transform:uppercase;color:#18936a}#about .about__left h2,#signUp .about__left h2{font-weight:bold;color:#fff;font-size:58px}#about .about__left p,#signUp .about__left p{margin-top:25px;color:#a8b1ae;font-size:20px}#about .about__left a.btn.btn-light,#signUp .about__left a.btn.btn-light{font-weight:bold;border:1px solid #01b767;padding:15px 35px;background:#01b767;border-radius:50px;-webkit-border-radius:50px;-moz-border-radius:50px;-ms-border-radius:50px;-o-border-radius:50px}#about .about__left .angle,#signUp .about__left .angle{margin-left:7px;font-size:20px}#about .about__left a.btn.btn-light:hover,#signUp .about__left a.btn.btn-light:hover{opacity:.8;background:#fff}#about .animated_img,#signUp .animated_img{animation:up-down 2s ease-in-out infinite alternate-reverse both;-webkit-animation:up-down 2s ease-in-out infinite alternate-reverse both}@-webkit-keyframes up-down{0%{transform:translateY(-20px);-webkit-transform:translateY(-20px);-moz-transform:translateY(-20px);-ms-transform:translateY(-20px);-o-transform:translateY(-20px)}100%{transform:translateY(20px);-webkit-transform:translateY(20px);-moz-transform:translateY(20px);-ms-transform:translateY(20px);-o-transform:translateY(20px)}}@keyframes up-down{0%{transform:translateY(-20px);-webkit-transform:translateY(-20px);-moz-transform:translateY(-20px);-ms-transform:translateY(-20px);-o-transform:translateY(-20px)}100%{transform:translateY(20px);-webkit-transform:translateY(20px);-moz-transform:translateY(20px);-ms-transform:translateY(20px);-o-transform:translateY(20px)}}#services{overflow:hidden;background:#000406;padding-top:120px;padding-bottom:80px}#services .services_heading h2{font-weight:bold;color:#fff;font-size:55px}#services .services_heading p{color:#a8b1ae}#services .card{background:#d9e2ff;transition:.3s;-webkit-transition:.3s;-moz-transition:.3s;-ms-transition:.3s;-o-transition:.3s}#services .card img{margin:0 auto;height:200px;width:100%}#services .card a.btn.btn-primary{padding:10px 25px;font-weight:bold;color:#000;border:1px solid #01b767;background:#01b767;border-radius:50px;-webkit-border-radius:50px;-moz-border-radius:50px;-ms-border-radius:50px;-o-border-radius:50px}#services .card a.btn.btn-primary:hover{border-color:#fff;opacity:.8;background:#fff}#services .card .angle{margin-left:7px;font-size:20px}#services .card:hover{transform:scale(1.02);-webkit-transform:scale(1.021);-moz-transform:scale(1.021);-ms-transform:scale(1.021);-o-transform:scale(1.021)}#services .row>*{padding-right:0}#signUp{overflow:hidden;background:#f8f8f8}#signUp a.btn.btn-light:hover{border-color:#01b767}#contactForm{overflow:hidden;padding-top:120px;padding-bottom:80px;background:#01b767}#contactForm .contact_form_box{background:#10141e;padding:50px 25px}#contactForm .contact_form_box h4{color:#fff}#contactForm .contact_form_box label{font-weight:600;color:#a8b1ae}#contactForm .contact_form_box .form-control{padding:13px 8px}#contactForm .contact_form_box button.form-control{font-weight:600;border:1px solid #01b767;color:#fff;background:#01b767}#contactForm .contact_form_box button.form-control:hover{opacity:.9;background:#01b767}#contactForm .contact_form_box button{outline:none;border:none;background:transparent;color:#a8b1ae}#footer{overflow:hidden;padding:50px 0;background:#10141e}#footer h5{font-weight:bold;font-size:24px;color:#fff}#footer ul li{margin:5px 0}#footer ul li a{color:#a8b1ae}#footerBottom{overflow:hidden;border-top:1px solid #a8b1ae;background:#10141e}#footerBottom .footer_logo{color:#01b566;font-size:50px}#footerBottom .copyright p{text-transform:capitalize;color:#a8b1ae}#footerBottom .name a{text-decoration:none;color:#01b566}#footerBottom .social_link ul li a{margin:0 15px;font-size:23px;color:#a8b1ae}
@media(max-width: 1200px){#banner{min-height:75vh}}@media(max-width: 991px){#about{padding-top:30px;padding-bottom:50px}#footerBottom .social_link ul li a{margin:0 7px}}@media(max-width: 767px){#banner .main__content p{padding:0}#footerBottom .social_link ul li a{font-size:13px}#footerBottom .footer_logo{font-size:30px}#footerBottom p{font-size:12px}}@media(max-width: 550px){#services .services_heading h2{font-size:48px}#about .animated_img,#signUp .animated_img{display:none}#footerBottom .footer_logo{display:none}}@media(max-width: 531px){#banner .main__content h2{font-size:50px}#about{padding-top:50px}}@media(max-width: 396px){#banner{padding:50px 0}}@media(max-width: 360px){#about .about__left h2,#signUp .about__left h2{font-size:50px}#services .row{--bs-gutter-x: 0}}
